import { Box, Button, Checkbox, FormControl, FormControlLabel, FormHelperText, FormLabel, IconButton, List, ListItem, ListItemIcon, ListItemText, Radio, RadioGroup, TextField, Typography, } from '@mui/material';
import React, { useRef, useState } from 'react';
import { Link, useNavigate } from 'react-router-dom';
import {format, differenceInDays } from 'date-fns';
import { v4 as uuid } from 'uuid';
import {SystemStyleObject} from '@mui/system'
import paakuva from '../paakuva.jpg';


interface Lomaketiedot {

    [key : string] : any;
    enimi? : string;
    snimi? : string;
    sahkoposti? : string;
    puhelinnumero?: string;
    tunti?: string;
}
interface Virheet extends Lomaketiedot {};

const tekstityyli : SystemStyleObject = {
    marginTop : "20px",
    marginBottom: "5px",
    display: "flex",
    flexDirection: "row"
}




const Ilmoittautuminen: React.FC<any> = ({}) => {

    const [virheilmoitukset, setVirheilmoitukset] = useState<Virheet>({});  

    const lomaketiedot : Lomaketiedot = useRef<Lomaketiedot>({});

    const navigate = useNavigate();

    const lomakeKasittelija = (e : React.FormEvent): void => {

        e.preventDefault();  
        let virheet : Virheet = {};

        if (!lomaketiedot.current.enimi || lomaketiedot.current.enimi.length <2) {
            virheet = {...virheet, enimi : "Etunimi tieto on sytettv"};
        }

        if (!lomaketiedot.current.snimi || lomaketiedot.current.snimi.length <2) {
            virheet = {...virheet, snimi : "Sukunimi tieto on sytettv"};
        }

        if (!lomaketiedot.current.puhelinnumero && !lomaketiedot.current.sahkoposti) {
            virheet = {...virheet, puhelinnumero : "Puhelinnumero tai shkposti on sytettv."};
            virheet = {...virheet, sahkoposti : "Puhelinnumero tai shkposti on sytettv."}
        }
       
        if (Object.entries(virheet).length > 0) {

            setVirheilmoitukset({...virheet});
        } else {
            navigate('/kiitos', { state: { ilmoittautumisTiedot: lomaketiedot.current } });
        }
        

    }
    
    
    const syoteKasittelija = (e: React.ChangeEvent<HTMLInputElement>) : void => {

        lomaketiedot.current [e.target.name] = e.target.value;
    }
   



    return (
        <div style={{ marginLeft: '50px' }}>

            <h1
            style={{
                color:'purple',
                
            }}>Tlt pset ilmoittautumaan tunneille</h1>
            <p
            style={{
                
            }}>Voit ilmoittautua yhdelle tunnille yhdell lomakkeella.<br/>
            Valitse ensin tunti, johon haluat ilmoittautua.<br/>
            Tmn jlkeen tytt vain yhteystietosi.<br/>
            Odotathan vahvistusta onnistuneesta ilmoittautumisesta.</p>

            <div style={{
            display:"flex",
                      
        }}>
        <img src={paakuva} className="tanssijat" alt="tss pitisi olla kuva" /> 
        </div>
            
            <form onSubmit={lomakeKasittelija}>
            <div
            style={{
                margin:'50px'
            }} >
            <h2 style={{
            color:'purple',
            textDecoration:'underline'
            
        }}>Valittavat tunnit</h2>
        <FormControl>
        <FormLabel
        style={{
            color:'purple',
            
        }}> Baletti</FormLabel>
            <RadioGroup>
                <FormControlLabel
                value="matunti1"
                label= "Maanantai klo 18-20 sali 1"
                control={<Radio
                            name="tunti"
                            size="small"
                            onChange= {syoteKasittelija}/>
                        }
                />
            
                <FormControlLabel
                value="to1"
                label= "Torstaisin klo 19-20 sali 2"
                control={<Radio
                            name="tunti"
                            size="small"
                            onChange= {syoteKasittelija}/>
                        }
                />

                
                <FormLabel
                style={{
                    color:'purple',
                    
                }}> Showtanssi</FormLabel>
                <FormControlLabel
                value="ti2"
                label= "Tiistaiisin klo 18-19 sali 1"
                control={<Radio
                            name="tunti"
                            size="small"
                            onChange= {syoteKasittelija}/>
                        }
                />

                <FormControlLabel
                value="to2"
                label= "Torstaisin klo 18-19 sali 2"
                control={<Radio
                            name="tunti"
                            size="small"
                            onChange= {syoteKasittelija}/>
                        }
                />
                <FormLabel
                style={{
                    color:'purple',
                    
                }}> Latinshow</FormLabel>
                <FormControlLabel
                value="ma02"
                label= "Maanantaisin klo 19-20 sali 1"
                control={<Radio
                            name="tunti"
                            size="small"
                            onChange= {syoteKasittelija}/>
                        }
                />
            <FormControlLabel
                value="ke01"
                label= "Keskiviikkoisin klo 18-19 sali 1"
                control={<Radio
                            name="nimi"
                            size="small"
                            onChange= {syoteKasittelija}/>
                        }
                />
            <FormLabel
            style={{
                color:'purple',
                
            }}> Jazz</FormLabel>
            <FormControlLabel
                value="ti03"
                label= "Tiistaisin klo 18-19 sali 1"
                control={<Radio
                            name="tunti"
                            size="small"
                            onChange= {syoteKasittelija}/>
                        }
                />

            <FormControlLabel
                value="to3"
                label= "Torstaisin klo 18-19 sali 1"
                control={<Radio
                            name="sarja"
                            size="small"
                            onChange= {syoteKasittelija}/>
                        }
                />

            </RadioGroup>
           

    </FormControl>


            <TextField
            sx= {tekstityyli}
            name= "enimi"
            label= "Nimi"
            placeholder='Etunimi'
            onChange={syoteKasittelija}
            error={Boolean(virheilmoitukset.enimi)}
            helperText={virheilmoitukset.enimi}
            /> 

            <TextField
            sx= {tekstityyli}
            name= "snimi"
            label= "Sukunimi"
            placeholder='Sukunimi'
            onChange={syoteKasittelija}
            error={Boolean(virheilmoitukset.snimi)}
            helperText={virheilmoitukset.snimi}
            />   

         <TextField
        sx= {tekstityyli}
        name= "puhelinnumero"
        label= "Puhelinnumero"
        placeholder='Puhelinnumero'      
        onChange={syoteKasittelija}
        error={Boolean(virheilmoitukset.puhelinnumero)}
        helperText={virheilmoitukset.puhelinnumero}
        />
    


        <TextField
       sx= {tekstityyli}
        name= "sahkoposti"
        label= "Shkoposti"
        placeholder='Shkpostiosoite'  
        onChange={syoteKasittelija}
        error={Boolean(virheilmoitukset.sahkoposti)}
        helperText={virheilmoitukset.sahkoposti}
        />

    <FormControl>      
      <FormControlLabel
        control={<Checkbox name="kayttoehdot" onChange={syoteKasittelija}/>}
        label="Olen hyvksynyt tanssikoulun snnt ja ehdot "
      />
       <FormHelperText error={Boolean(virheilmoitukset.kayttoehdot)}>
        {virheilmoitukset.kayttoehdot}
      </FormHelperText>
    </FormControl> 

    <FormControl>      
      <FormControlLabel
        control={<Checkbox name="kuvauslupa" onChange={syoteKasittelija}/>}
        label="Tanssikoulu saa julkaista nettisivuillaan minusta otettuja kuvia"
      />
    </FormControl> 

        <Box sx={{ marginTop: 'auto'

         }}>
        <Button
        color="secondary"
        type="submit"
        variant="contained"
        size="medium"
        >Lhet ilmoittautuminen</Button>

        </Box>

       

       

            </div>
            </form>

            <div style={{
                margin:'50px'
            }}>

            <Link to="/">
                <Button color="secondary"
                type="submit"
                variant="contained"
                size="medium"
               >
                    Takaisin etusivulle
                </Button>
                </Link>
                </div>

            </div>)
};

    export default Ilmoittautuminen;