import { AppBar, Container, CssBaseline, Drawer, IconButton, List, ListItemButton, ListItemIcon, ListItemText, Toolbar, Typography } from '@mui/material';
import MenuIcon from '@mui/icons-material/Menu';
import GavelIcon from '@mui/icons-material/Gavel';
import AppRegistrationIcon from '@mui/icons-material/AppRegistration';
import TheaterComedyIcon from '@mui/icons-material/TheaterComedy';
import ShoppingCartIcon from '@mui/icons-material/ShoppingCart';
import BookOnlineIcon from '@mui/icons-material/BookOnline';
import ContactPageIcon from '@mui/icons-material/ContactPage';
import HomeIcon from '@mui/icons-material/Home';
import React, { useState } from 'react';
import { Link } from 'react-router-dom';

const Valikko: React.FC<any> = (props : any) : React.ReactElement =>{

    const [ValikkoAuki, setValikkoAuki] = useState <boolean>(false);


    return (

        <CssBaseline>
    
        <AppBar position='static'
        color="secondary">

            <Toolbar>
            <IconButton
                color="inherit"
                edge="start"
                onClick={() => setValikkoAuki(true)}>
            <MenuIcon/>
            </IconButton>

            <Drawer
                open={ValikkoAuki}
                onClose={() => setValikkoAuki(false)}>

                   <List
                   sx={{
                    width : "220px",
                    marginTop : "50px"
                   }}
                   onClick={() => setValikkoAuki(false)}>
                    <ListItemButton
                    component={Link}
                    to="/">

                        <ListItemIcon>
                            <HomeIcon/>
                        </ListItemIcon>
                        <ListItemText primary="Etusivu"/>

                    </ListItemButton>
                    <ListItemButton
                     component={Link}
                     to="/tunnit">
                        <ListItemIcon>
                            <TheaterComedyIcon/>
                        </ListItemIcon>
                        <ListItemText primary="Tuntikuvaukset"/>
                        
                    </ListItemButton>
                    <ListItemButton
                     component={Link}
                     to="/ilmoittautuminen">
                        <ListItemIcon>
                            <AppRegistrationIcon/>
                        </ListItemIcon>
                        <ListItemText primary="Ilmoittautuminen"/>
                        
                    </ListItemButton>
                    <ListItemButton
                     component={Link}
                     to="/kauppa">
                        <ListItemIcon>
                            <ShoppingCartIcon/>
                        </ListItemIcon>
                        <ListItemText primary="Kauppa"/>
                        
                    </ListItemButton>

                    <ListItemButton
                     component={Link}
                     to="/ajanvaraus">
                        <ListItemIcon>
                            <BookOnlineIcon/>
                        </ListItemIcon>
                        <ListItemText primary="Ajanvaraukseen"/>
                        
                    </ListItemButton>

                    <ListItemButton
                     component={Link}
                     to="/yhteydenotto">
                        <ListItemIcon>
                            <ContactPageIcon/>
                        </ListItemIcon>
                        <ListItemText primary="Ota yhteytt"/>
                        
                    </ListItemButton>
                 
                    
                    </List> 
                    
                
            </Drawer>
            <Typography
                component="div"
                sx={{fontSize: "16pt", flexGrow: 1}}>
                Valikko
            </Typography>
            </Toolbar>
      
    
        </AppBar>
        </CssBaseline>
    );
    
    }
    export default Valikko;