import React, { useRef, useState } from 'react';
import { Link, useNavigate } from 'react-router-dom';
import LocalPhoneIcon from '@mui/icons-material/LocalPhone';
import Send from '@mui/icons-material/Send';
import { TextField, Button, Box, ListItemIcon } from '@mui/material';
import { SystemStyleObject } from '@mui/system';

interface Viestitiedot {
    [key: string]: any;
    enimi?: string;
    snimi?: string;
    sahkoposti?: string;
    puhelinnumero?: string;
    viesti?: string;
}

interface Virhe extends Viestitiedot {}

const tekstityyli: SystemStyleObject = {
    marginTop: '20px',
    marginBottom: '5px',
    display: 'flex',
    flexDirection: 'row'
};

const Yhteydenotto: React.FC<any> = () => {
    const [virheilmoitukset, setVirheilmoitukset] = useState<Virhe>({});
    const viestilomake = useRef<Viestitiedot>({});
    const navigate = useNavigate();

    const viestinKasittelija = (e: React.FormEvent): void => {
        e.preventDefault();

        let virheet: Virhe = {};

        if (!viestilomake.current.enimi || viestilomake.current.enimi.length < 2) {
            virheet = { ...virheet, enimi: 'Etunimi tieto on sytettv' };
        }

        if (!viestilomake.current.snimi || viestilomake.current.snimi.length < 2) {
            virheet = { ...virheet, snimi: 'Sukunimi tieto on sytettv' };
        }

        if (!viestilomake.current.puhelinnumero && !viestilomake.current.sahkoposti) {
            virheet = {
                ...virheet,
                puhelinnumero: 'Puhelinnumero tai shkposti on sytettv.',
                sahkoposti: 'Puhelinnumero tai shkposti on sytettv.'
            };
        }

        if (Object.keys(virheet).length > 0) {
            setVirheilmoitukset({ ...virheet });
        } else {
            navigate('/kiitosuk');
        }
    };

    const syoteKasittelija = (e: React.ChangeEvent<HTMLInputElement>): void => {
        viestilomake.current[e.target.name] = e.target.value;
    };

    return (
        <div style={{ marginLeft: '20px' }}>
            <h1 style={{ color: 'purple', textDecoration: 'underline' }}>OTA YHTEYTT MEIHIN</h1>
            <p>Voit olla meihin yhteydess puhelimitse tai shkpostitse, taikka oheisella yhteydenottolomakkeella.</p>
            <br />
            <ListItemIcon>
                <LocalPhoneIcon style={{ marginRight: '10px' }} />
                044-123 123
            </ListItemIcon>
            <br />
            <ListItemIcon>
                <Send style={{ marginRight: '10px' }} />
                info@keksittyosoite.fi
            </ListItemIcon>
            <form onSubmit={viestinKasittelija}>
                <h2 style={{ color: 'purple', textDecoration: 'underline' }}>Yhteydenottolomake</h2>
                <TextField
                    sx={tekstityyli}
                    name="enimi"
                    label="Nimi"
                    placeholder="Etunimi"
                    onChange={syoteKasittelija}
                    error={Boolean(virheilmoitukset.enimi)}
                    helperText={virheilmoitukset.enimi}
                />
                <TextField
                    sx={tekstityyli}
                    name="snimi"
                    label="Sukunimi"
                    placeholder="Sukunimi"
                    onChange={syoteKasittelija}
                    error={Boolean(virheilmoitukset.snimi)}
                    helperText={virheilmoitukset.snimi}
                />
                <TextField
                    sx={tekstityyli}
                    name="puhelinnumero"
                    label="Puhelinnumero"
                    placeholder="Puhelinnumero"
                    onChange={syoteKasittelija}
                    error={Boolean(virheilmoitukset.puhelinnumero)}
                    helperText={virheilmoitukset.puhelinnumero}
                />
                <TextField
                    sx={tekstityyli}
                    name="sahkoposti"
                    label="Shkoposti"
                    placeholder="Shkpostiosoite"
                    onChange={syoteKasittelija}
                    error={Boolean(virheilmoitukset.sahkoposti)}
                    helperText={virheilmoitukset.sahkoposti}
                />
                <br/>
                <TextField
                    id="outlined-multiline-static"
                    label="Viesti"
                    multiline
                    rows={4}
                    defaultValue=''
                    onChange={syoteKasittelija}
                    error={Boolean(virheilmoitukset.viesti)}
                    helperText={virheilmoitukset.viesti}
                />
                <br/>
                <Box sx={{ marginTop: 'auto' }}>
                    <br/>
                    <Button color="secondary" type="submit" variant="contained" size="medium">
                        Lhet viesti
                    </Button>
                </Box>
            </form>

            <br/>
            <div>

            <Link to="/">
                <Button color="secondary"
                type="submit"
                variant="contained"
                size="medium"
               >
                    Takaisin etusivulle
                </Button>
                </Link>
                </div>
        </div>
    );
};

export default Yhteydenotto;