import { Box, Button, Dialog, DialogContent, DialogTitle, FormControl, IconButton, InputLabel, List, ListItem, ListItemIcon, ListItemText, MenuItem, Select, SelectChangeEvent, Slider, TextField, Typography } from '@mui/material';
import React, { ReactNode, useState } from 'react';
import { Link } from 'react-router-dom';
import {format, differenceInDays } from 'date-fns';
import { v4 as uuid } from 'uuid';
import DeleteIcon from '@mui/icons-material/Delete';
import SaveIcon from '@mui/icons-material/Save';




const Kauppa: React.FC<any> = () => {

    const [hinta, setHinta] = React.useState('');
    const [lopullinenHinta, setLopullinenHinta] = React.useState(0);
    const [nimi, setNimi] = React.useState('');
    const [sahkoposti, setSahkoposti] = React.useState('');
    const [avoinDialogi, setAvoindialogi] = React.useState(false);

    const handleChange = (event: SelectChangeEvent) => {
        setHinta(event.target.value as string);
    };

 
    const ostaTuote = () => {
        setAvoindialogi(true);
    }

    const handleCloseDialog = () => {
        setAvoindialogi(false);
    };

    
 

   

    return (

        <div>
        <div style={{ marginLeft: '20px' }}>

            <h1 style={{
                color:'purple'
            }}>Hienoa lysit verkkokauppaamme!</h1>

            <p> Alta lytyy mahdollisuudet ostaa lahjakortti kolmella eri summavaihtoehdolla. <br/>
            Voit valita mys maksettavaksesi syys- tai kevtlukukauden sek ostaa 10 kerran tunnin <br/>
            Lisksi sinulla on mahdollisuus maksaa kertatunti taikka tilaustunti.</p>
            </div>

            <h2 style={{
                color:'purple',
                marginLeft:'20px'
            }}>Ostettavissa olevat tuotteet</h2>

            <Box sx={{ maxWidth: 400,
                marginLeft: 2
            }}>
                <FormControl fullWidth>
                    <InputLabel id='tuotevalinta'> Valitse tuote</InputLabel>
                    <Select
                        labelId='tuotevalinta'
                        id='tuotevalinta'
                        value={hinta}
                        label='tuote'
                        onChange={handleChange}>
                            <MenuItem value={250}>Syyslukukausi 250 </MenuItem>
                            <MenuItem value={250}>Kevtlukukausi 250 </MenuItem>
                            <MenuItem value={200}>Kymmenen kerran kortti 200 </MenuItem>
                            <MenuItem value={20}>Kertatunti 20 </MenuItem>
                            <MenuItem value={50}>Lahjakortti 50 </MenuItem>
                            <MenuItem value={100}>Lahjakortti 100 </MenuItem>
                            <MenuItem value={150}>Lahjakortti 150 </MenuItem>
                            <MenuItem value={80}>Tilaustunti 30min 80</MenuItem>
                            <MenuItem value={120}>Tilaustunti 45min 120</MenuItem>
                            <MenuItem value={100}>Tilaustunti 60min 100</MenuItem>

                        </Select>
                </FormControl>
            </Box>
            

            <Box>
         <h3
         style={{
            color:'purple',
            marginLeft:'20px'
         }}>Ostoksesi kokonaishinta: {hinta} </h3>
         </Box>

         <Box
        component="form"
        sx={{
          '& > :not(style)': { m: 1, width: '25ch' },
         }}
        noValidate
        autoComplete="off"
         >
        <TextField id="outlined-basic" label="Tilaajan nimi" variant="outlined" value={nimi} onChange={(e) => setNimi(e.target.value)}/>
        <TextField id="outlined-basic" label="Tilaajan shkposti" variant="outlined" value={sahkoposti} onChange= {(e) => setSahkoposti(e.target.value)} />
    </Box>

         <br/><br/>


         <Button
         style={{
            marginLeft:'20px'
         }}
          variant="contained"
          onClick={ostaTuote}
          color="secondary"
          size="medium"
          >Tilaa ostoksesi</Button>


         <Dialog open={avoinDialogi} onClose={handleCloseDialog}>
        <DialogTitle>Kiitos ostoksestasi ja tervetuloa tanssimaan!
            Saat tilausvahvistuksen antamaasi shkpostiosoitteeseen
        </DialogTitle>
        <DialogContent>
        <p>Ostoksen loppusumma: {hinta} </p>
        <Typography>Tilaajan nimi: {nimi}</Typography>
        <Typography>Tilaajan shkposti: {sahkoposti}</Typography>
        </DialogContent>
       
        
      </Dialog>

      
      <Link to="/">
                <Button color="secondary"
                type="submit"
                variant="contained"
                size="medium"
                style={{
                    marginLeft:'10px'
                }}>
                    Takaisin etusivulle
                </Button>
                </Link>
            
            </div>
            )
};

    export default Kauppa;