import React from 'react';
import { useLocation } from 'react-router-dom';


const Kiitos: React.FC<any> = (props : any) : React.ReactElement =>{

    const location = useLocation();
    const { ilmoittautumisTiedot } = location.state || {};

return (

    <div
    style={{
        marginLeft:"10px"

    }}>

    <p>Kiitos ja tervetuloa tanssimaan kanssamme timanttisessa porukassa! <br/>
    Tarkistathan viel alta, ett kaikki tietosi pitvt paikkansa.</p>
    
    {ilmoittautumisTiedot && (
                <div>
                    <h3>Yhteenveto ilmoittautumisestasi:</h3>
                    <p>Etunimi: {ilmoittautumisTiedot.enimi}</p>
                    <p>Sukunimi: {ilmoittautumisTiedot.snimi}</p>
                    <p>Asuinpaikka {ilmoittautumisTiedot.asuinpaikka}</p>
                    {ilmoittautumisTiedot.puhelinnumero && (
                        <p>Puhelinnumero: {ilmoittautumisTiedot.puhelinnumero}</p>
                    )}
                    {ilmoittautumisTiedot.sahkoposti && (
                        <p>Shkposti: {ilmoittautumisTiedot.sahkoposti}</p>
                    )}
                    <p>Tunti johon osallistun {ilmoittautumisTiedot.tunti}</p>
                </div>
            )}
  

    </div>
);

}
export default Kiitos;