import { Box, Button, FormControl, IconButton, InputLabel, List, ListItem, ListItemIcon, ListItemText, MenuItem, Select, TextField, Typography } from '@mui/material';
import React, { useRef, useState } from 'react';
import { Link, NavigateFunction, useNavigate } from 'react-router-dom';
import {format, differenceInDays } from 'date-fns';
import { v4 as uuid } from 'uuid';
import { DateTimePicker, LocalizationProvider } from '@mui/x-date-pickers';
import { fi } from 'date-fns/locale';
import { AdapterDateFns } from '@mui/x-date-pickers/AdapterDateFnsV3'

interface Props {

    aika : Aika[];
    setAika : (aika : Aika []) => void;

}

const Ajanvaraus: React.FC<Props> = ({aika, setAika}) : React.ReactElement => {

    const [pvmAika, setPvmAika] = useState<Date>(new Date());
    const [palvelu, setPalvelu] = useState<string>('');
    const [enimi, setEnimi] = useState<string>('');
    const [snimi, setSnimi] = useState<string>(''); 

    const palvelut = [
        { value: '30', label: 'Tilaustunti 30min' },
        { value: '45', label: 'Tilaustunti 45min' },
        { value: '60', label: 'Tilaustunti 60min' },
    ];


    const navigate : NavigateFunction = useNavigate();

    const enimiRef: React.MutableRefObject<HTMLInputElement | undefined> = useRef<HTMLInputElement>();
    const snimiRef: React.MutableRefObject<HTMLInputElement | undefined> = useRef<HTMLInputElement>();
    const pnroRef: React.MutableRefObject<HTMLInputElement | undefined> = useRef<HTMLInputElement>();
    const spostiRef: React.MutableRefObject<HTMLInputElement | undefined> = useRef<HTMLInputElement>();

    const lisaaAika = (): void => {
        const uusienimi = enimiRef.current?.value || 'Nimetn kaiffari';
        const uusisnimi = snimiRef.current?.value || 'Jane Doe';
        const uusipnro = pnroRef.current?.value || 'Tuntematon numero';
        const uusisposti = spostiRef.current?.value || 'Tuntematon shkposti';

        const uusiAika: Aika = {

            
            enimi: uusienimi,
            snimi: uusisnimi,
            pnumero: uusipnro,
            sposti: uusisposti,
            aika: pvmAika,
            palvelu: palvelu
        };
    
        setAika([...aika, uusiAika]);

        navigate("/yhteenveto", { state: { uusiAika } });
    }


    return (
        <div style={{ marginLeft: '20px' }}>

        <h1 style={{
            color:'purple'
        }}>Varaa uusi aika</h1>
        
        <LocalizationProvider dateAdapter={AdapterDateFns} adapterLocale={fi}>

        <DateTimePicker 
        label="Aika"
        value={pvmAika}
        onChange={(uusiPvm: Date | null) => setPvmAika(uusiPvm || new Date())}
        slots={{
            textField: (params) => (
                <TextField
                    {...params}
                    variant="outlined"
                    
                />
            ),
        }}   
    />

        <Box sx={{ maxWidth: 400,
                
            }}>
                <FormControl fullWidth>
                    <InputLabel id='tuotevalinta'> Valitse palvelu</InputLabel>
                    <Select
                        labelId='palveluvalinta'
                        id='palveluvalinta'
                        value={palvelu}
                        label='palvelu'
                        onChange={(event) => setPalvelu(event.target.value as string)}>
                             {palvelut.map((item) => (
                        <MenuItem key={item.value} value={item.value}>
                            {item.label}
                        </MenuItem>
                    ))}

                        </Select>
                </FormControl>
            </Box>

            
        <TextField 
        label="Etunimi"
        inputRef={enimiRef}
        variant="outlined"
        fullWidth={true}
        sx={{marginBottom : "10px"}}
             />
             <TextField 
                 label="Sukunimi"
                 inputRef={snimiRef}
                 variant="outlined"
                 fullWidth={true}
                 sx={{marginBottom : "10px"}}
                />


        <TextField
        name= "puhelinnumero"
        label= "Puhelinnumero"
        inputRef={pnroRef}
        variant="outlined"
        fullWidth={true}
        sx={{marginBottom : "10px"}}
       />
        
    
        <TextField
        name= "sahkoposti"
        label= "Shkposti"
        inputRef={spostiRef}
        variant="outlined"
        fullWidth={true}
        sx={{marginBottom : "10px"}}
       />
          <Button
          color='secondary'
        variant="contained"
        
        onClick={lisaaAika}
        >Varaa aika</Button>  
</LocalizationProvider>

<br/><br/>

            <Link to="/">
                <Button color="secondary"
                type="submit"
                variant="contained"
                size="medium"
                
               >
                    Takaisin etusivulle
                </Button>
                </Link>
                


            </div>)
};

    export default Ajanvaraus;