import { Button, IconButton, List, ListItem, ListItemIcon, ListItemText, Typography } from '@mui/material';
import React from 'react';
import { Link, useLocation } from 'react-router-dom';
import {format, differenceInDays } from 'date-fns';
import { v4 as uuid } from 'uuid';
import DeleteIcon from '@mui/icons-material/Delete';
import SaveIcon from '@mui/icons-material/Save';
import { DateTimePicker, LocalizationProvider } from '@mui/x-date-pickers';
import { fi } from 'date-fns/locale';
import { AdapterDateFns } from '@mui/x-date-pickers/AdapterDateFnsV3'


interface Props {

    aika : Aika[];
    setAika : (aika : Aika []) => void;

}


const Yhteenveto: React.FC<Props> = ({ aika, setAika }) => {
    const location = useLocation();
    const uusiAika: Aika = location.state?.uusiAika;



    if (!uusiAika) {
        return <div>Ladataan...</div>; // Tai muu ksittely, jos tietoja ei lydy
    }

    const lajitteleAjat = [...aika].sort((a, b) => {
        const aikaA = new Date(a.aika);
        const aikaB = new Date(b.aika);
        return aikaB.getTime() - aikaA.getTime(); // Uusin ensin
    });

    // Nyt tiedot
    return (
        <div style={{ marginLeft: '20px' }}>

            <h1 style={{
                color:'purple'
            }}>Yhteenveto varatuista ajoista</h1>
         
    <List>
             {lajitteleAjat.map((aika, idx: number) => (
                <ListItem key={idx}>
                <ListItemText
                primary={
                
                    <>
                        <Typography variant="body1">Valittu aika: {aika.palvelu}</Typography>
                        <Typography variant="body1">Varaajan etunimi: {aika.enimi}</Typography>
                        <Typography variant="body1">Varaajan sukunimi: {aika.snimi}</Typography>
                        <Typography variant="body1">Puhelinnumero: {aika.pnumero}</Typography>
                        <Typography variant="body1">Shkposti: {aika.sposti}</Typography>
                        <Typography variant="body1">Varattu aika: {format(new Date(aika.aika), "d.M.y HH:mm")}</Typography>
                    </>
                }
            />
            </ListItem>
        ))}
     </List>


     <Link to="/ajanvaraus">
                <Button color="secondary"
                type="submit"
                variant="contained"
                size="medium"
               >
                    Takaisin ajanvaraukseen
                </Button>
                </Link>
                
        </div>
    );
};

    export default Yhteenveto;