import { Box, Button, IconButton, List, ListItem, ListItemIcon, ListItemText, Rating, Typography } from '@mui/material';
import React from 'react';
import { Link } from 'react-router-dom';
import {format, differenceInDays } from 'date-fns';
import { v4 as uuid } from 'uuid';
import DeleteIcon from '@mui/icons-material/Delete';
import SaveIcon from '@mui/icons-material/Save';
import StarIcon from '@mui/icons-material/Star';
import paakuva from '../paakuva.jpg';





const Etusivu: React.FC<any> = ({}) => {

    const labels: { [index: string]: string } = {
        0.5: 'Ei missn nimess',
        1: 'Hydytn',
        1.5: 'Tyls',
        2: 'Tyls+',
        2.5: 'Ok',
        3: 'Semi Ok+',
        3.5: 'Hyv',
        4: 'Hyv mutta ei tydellinen',
        4.5: 'Erinomainen',
        5: '10- Todellakin suosittelen',
      };
      
      function getLabelText(value: number) {
        return `${value} Star${value !== 1 ? 's' : ''}, ${labels[value]}`;
      }

      const [value, setValue] = React.useState<number | null>(4);
      const [hover, setHover] = React.useState(-1);


    return (
        <div style={{ marginLeft: '20px',
            maxWidth:'1020px'
         }}>

            <div>
                <h1
                style={{
                    color:"purple",
                    textAlign:'center',
                    
                }}>Tervetuloa Tanssimanian pariin!</h1>
            </div>
        <div style={{
            display:"flex",
            justifyContent:"center"            
        }}>
        <img src={paakuva} className="tanssijat" alt="tss pitisi olla kuva" /> 
        </div>
        <div>
            <p style={{
                textAlign: 'center'
            }}>Navigoithan vasemmalta lytyvst valikosta tanssilajien esittelyyn,<br/>
            tunneille ilmoittautumiseen,
            tekemn ostoksia kauppaan,<br/>
             sek varaamaan aikoja yksityistunneille.<br/><br/>
             Tanssimania tanssikoulu on aikuisille suunnattu harrastuspaikka. <br/>
             Tunnit osiosta pset tarkastelemaan tuntivaihtoehdot, <br/>
             josko sielt lytyisikin juuri sinulle sopiva tunti.</p>
        </div>

        <h3 style={{
            color:'purple',
            textAlign: 'center',
        }}>TANSSIMISIIN !</h3>

        <br/><br/>


    <h3 style={{
            color:'purple',
            marginRight:'10px',
            textAlign:'center'
        }}>Ehtisitk viel antamaan thden?</h3>
        <br/>

    <Box
      sx={{
        width: 560,
        display: 'flex',
        alignItems: 'center',
        marginLeft:'330px'
      }}
    >
        
      <Rating
      
        name="hover-feedback"
        value={value}
        precision={0.5}
        getLabelText={getLabelText}
        onChange={(event, newValue) => {
          setValue(newValue);
        }}
        onChangeActive={(event, newHover) => {
          setHover(newHover);
        }}
        emptyIcon={<StarIcon style={{ opacity: 0.55 }} fontSize="inherit" />}
        
      />
      {value !== null && (
        <Box sx={{ ml: 2 }}>{labels[hover !== -1 ? hover : value]}</Box>

      )}
      
    </Box>

    <br/>
    <br/>


            </div>)
};

    export default Etusivu;